import Vue from 'vue';
import Vuex from 'vuex';
import { getUserInfo, login } from '@/api/index.js';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLoading: false, // 请求正在加载
    userInfo: {
      picture: '',
      nickname: '',
      username: '',
      mobile: '',
      idCard: '',
      token: window.sessionStorage.getItem('TOKEN') || '',
      openId: '',
    },
    bankcard: {
      username: '', // 用户名称
      idCard: '', // 用户身份证码
      bankcardNo: '', // 银行卡号
      bankcardCode: '', // 银行编号
      bankcardName: '', // 银行名称
      mobile: '', // 用户手机号
      logo: '', // 银行logo
    },
  },
  mutations: {
    SHOW_LOADING(state) {
      state.isLoading = true;
    },
    CLOSE_LOADING(state) {
      state.isLoading = false;
    },
    UPDATE_USERINFO(state, obj) {
      Object.assign(state.userInfo, obj);
    },
    UPDATE_BANKCARD(state, obj) {
      Object.assign(state.bankcard, obj);
    },
  },
  actions: {
    GETUSERINFO({ commit }) {
      return new Promise((resolve, reject) => {
        getUserInfo().then((result) => {
          commit('UPDATE_USERINFO', result);
          commit('UPDATE_BANKCARD', {
            username: result.username,
            idCard: result.idCard,
            mobile: result.mobile,
          });
          resolve('ok');
        }).catch(err => reject(err));
      });
    },
    LOGIN({ commit, dispatch }, openId) {
      return new Promise((resolve, reject) => {
        login(openId).then((result) => {
          window.sessionStorage.setItem('TOKEN', result);
          commit('UPDATE_USERINFO', {
            openId,
            token: result,
          });
          dispatch('GETUSERINFO').finally(console.log);
          resolve('ok');
        }).catch(err => reject(err));
      });
    },
  },
});
