export function queryString(name) {
  const reg = new RegExp(`(^|&)${name}=([^&]*)(&|$)`);
  const str = window.location.href.indexOf('#') < window.location.href.indexOf('?')
    ? window.location.href
    : window.location.search;
  const searchStr = str.split('?')[1];
  if (searchStr) {
    const r = searchStr.match(reg);
    if (r != null) return decodeURIComponent(r[2]);
    return null;
  }
  return null;
}

let isRunning = false;
export function parabola(startElem, endElem, moveElemX, moveElemY) {
  return new Promise((resolve, reject) => {
    if (isRunning) {
      reject(new Error('elem is moving'));
    }
    // 开始点和目标点的位置
    const startBound = startElem.getBoundingClientRect();
    const endBound = endElem.getBoundingClientRect();
    // 两者中心点的水平垂直距离
    const offsetX = endBound.left
      + endBound.width / 2
      - (startBound.left + startBound.width / 2);
    const offsetY = endBound.top
      + endBound.height / 2
      - (startBound.top + startBound.height / 2);
    // 页面滚动尺寸
    const scrollTop = document.documentElement.scrollTop || document.body.scrollTop || 0;
    const scrollLeft = document.documentElement.scrollLeft || document.body.scrollLeft || 0;
    // 移动元素初始定位
    moveElemX.style.left = `${startBound.left + scrollLeft + startElem.clientWidth / 2}px`;
    moveElemX.style.top = `${startBound.top + scrollTop + startElem.clientHeight / 2}px`;
    // 开始动画
    moveElemX.style.transform = `translateX(${offsetX}px)`;
    moveElemY.style.transform = `translateY(${offsetY}px)`;
    // 动画标志量
    isRunning = true;
    setTimeout(() => {
      isRunning = false;
      resolve('ok');
    }, 490);
  }).catch(err => console.log(err));
}
