import Vue from 'vue';
import Router from 'vue-router';
import store from '@/store.js';
import { queryString } from '@/assets/js/utils.js';

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'store',
      component: () => import('@/views/store.vue'),
      meta: {
        title: '维信分期宝',
      },
    },
    {
      path: '/store-detail/:id',
      name: 'store-detail',
      component: () => import('@/views/store-detail.vue'),
      meta: {
        title: '维信分期宝',
      },
    },
    {
      path: '/course-detail/:id',
      name: 'course-detail',
      component: () => import('@/views/course-detail.vue'),
      meta: {
        title: '课程详情',
      },
    },
    {
      path: '/order-course/:id',
      name: 'order-course',
      component: () => import('@/views/order-course.vue'),
      meta: {
        title: '开通课程',
      },
    },
    {
      path: '/bankcard-info',
      name: 'bankcard-info',
      component: () => import('@/views/bankcard-info.vue'),
      meta: {
        title: '添加银行卡',
      },
    },
    {
      path: '/pay-calculated',
      name: 'pay-calculated',
      component: () => import('@/views/pay-calculated.vue'),
      meta: {
        title: '开通续费',
      },
    },
    {
      path: '/pay-success',
      name: 'pay-success',
      component: () => import('@/views/pay-success.vue'),
      meta: {
        title: '开通续费',
      },
    },
    {
      path: '/pay-fail',
      name: 'pay-fail',
      component: () => import('@/views/pay-fail.vue'),
      meta: {
        title: '开通续费',
      },
    },
    {
      path: '/myOrder',
      name: 'myOrder',
      component: () => import('@/views/myOrder.vue'),
      meta: {
        title: '我的订单',
      },
    },
    {
      path: '/orderDetail/:orderId',
      name: 'orderDetail',
      component: () => import('@/views/orderDetail.vue'),
      meta: {
        title: '订单详情',
      },
    },
    {
      path: '/autoRenew/:orderId/:renew/:billDay',
      name: 'autoRenew',
      component: () => import('@/views/autoRenew.vue'),
      meta: {
        title: '自动续费',
      },
    },
    {
      path: '/closeRenew',
      name: 'closeRenew',
      component: () => import('@/views/closeRenew.vue'),
      meta: {
        title: '关闭续费',
      },
    },
    {
      path: '/comfirmOrder/:merchandiseId/:payType/:payId/:installNum',
      name: 'comfirmOrder',
      component: () => import('@/views/comfirmOrder.vue'),
      meta: {
        title: '确认订单',
      },
    },
    {
      path: '/bankcardList',
      name: 'bankcardList',
      component: () => import('@/views/bankcardList.vue'),
      meta: {
        title: '银行卡',
      },
    },
    {
      path: '/404',
      name: '404',
      component: () => import('@/views/404.vue'),
    },
    {
      path: '*',
      redirect: '/404',
    },
    {
      path: '/instalmentContract',
      name: 'instalmentContract',
      component: () => import('@/views/instalmentContract.vue'),
      meta: {
        title: '分期协议',
      },
    },
    {
      path: '/QrCode',
      name: 'QrCode',
      component: () => import('@/views/QrCode.vue'),
      meta: {
        title: '扫一扫',
      },
    },
  ],
});

const whiteList = ['/login', '/404']; // 不重定向白名单
router.beforeEach((to, from, next) => {
  const meta = to.meta || {};
  document.title = meta.title || '维信分期宝';
  if (whiteList.includes(to.path)) {
    next();
  } else if (!store.state.userInfo.token) {
    // 获取TOKEN
    store.dispatch('LOGIN', queryString('openId')).then(() => {
      next();
    }).catch((err) => {
      console.error(err);
      next('/404');
    });
  } else if (!(store.state.userInfo.nickname || store.state.userInfo.username)) {
    // 用户刷新页面
    store.dispatch('GETUSERINFO').then(() => {
      next();
    }).catch((err) => {
      console.error(err);
      next('/404');
    });
  } else {
    next();
  }
});

export default router;
