import Vue from 'vue';
import {
  Loading,
  Cell,
  CellGroup,
  Lazyload,
  Swipe,
  SwipeItem,
  Field,
  Button,
  Checkbox,
  Icon,
  Popup,
  Area,
  Dialog,
  NumberKeyboard,
  Uploader,
  SwitchCell,
  Picker,
  Toast,
  Tag,
  Tab,
  Tabs,
  Search,
  Card,
  Panel,
  PullRefresh,
} from 'vant';

// options 为可选参数，无则不传
import errorImg from '../assets/images/error.png';

const options = {
  preLoad: 1.3,
  attempt: 1,
  loading: errorImg,
  error: errorImg,
};

Vue.use(Loading);
Vue.use(Cell).use(CellGroup);
Vue.use(Lazyload, options);
Vue.use(Swipe).use(SwipeItem);
Vue.use(Field);
Vue.use(Button);
Vue.use(Checkbox);
Vue.use(Icon);
Vue.use(Popup);
Vue.use(Area);
Vue.use(Dialog);
Vue.use(NumberKeyboard);
Vue.use(Uploader);
Vue.use(SwitchCell);
Vue.use(Picker);
Vue.use(Toast);
Vue.use(Tag);
Vue.use(Search);
Vue.use(Tab).use(Tabs);
Vue.use(Card);
Vue.use(Panel);
Vue.use(PullRefresh);
