import request from '@/plugins/axios.js';

/**
 * 登录注册接口
 *
 * @export
 * @param {*} openId 微信号标识
 * @param {*} mobileNo 手机号
 * @param {*} identifyingCode 验证码
 * @returns promise
 */
export function login(loginName, loginType = '200112') {
  return request({
    url: '/server/cust/sign/in',
    method: 'post',
    params: {
      interceptorHint: 'needless',
    },
    data: {
      loginName,
      loginType,
    },
  });
}

/**
 * 发送短信验证码接口
 *
 * @export
 * @param {*} phone 微信标识
 * @param {*} type 手机号
 * @returns promise
 */
export function getCaptcha(phone, type) {
  return request({
    url: '/server/send/sms',
    method: 'post',
    data: {
      phone,
      type,
    },
    timeout: 10000,
  });
}

