/**
 * 银行卡验证接口
 *
 * @export
 * @param {*} realName 客户姓名
 * @param {*} idCode 身份证号
 * @param {*} bankCardNo 银行卡号
 * @param {*} phoneNum 预留手机号
 * @param {*} accountBank 所属银行
 * @returns
 */

export function validateBankCard(
  userName,
  idCardNo,
  bankCardNo,
  bankCardCode,
  bankCardMobile,
  verifyCode,
) {
  return request({
    url: '/server/cust/bank/card/add',
    method: 'post',
    data: {
      userName,
      idCardNo,
      bankCardNo,
      bankCardCode,
      bankCardMobile,
      verifyCode,
    },
  });
}

